import Papa from 'papaparse';
import moment from './lib/moment';
import EventContent from './eventItem';
import './lib/flatMap';
import './lib/flat';

/**
 * CSVJSON
 */
export default function parseContent(inputCsv) {
  const results = Papa.parse(inputCsv, {
    header: true,
    skipEmptyLines: true,
    beforeFirstChunk: chunk => chunk.replace(/.*,{10,}\n/gm, ''), // CSV
    transform: value => value.replace(/(\r\n|\n|\r|\s)/g, ''), // 
  });

  return results.data.flatMap((item) => {
    const eventCollection = [];
    const datePattern = /(\d{1,2})(?:)?(\d{1,2})?(?:)?(?:.*?[|(].+?[|)])?(?:.*?(|||))?/g; // https://regex101.com/r/hhA8KY/16
    const datesForEvent = [...item[''].matchAll(datePattern)];
    const year = moment().format('YYYY');

    datesForEvent.forEach(([, month, day, divider], i) => {
      const newEvent = new EventContent(); // 
      const startAndEndDates = []; // 
      day = day || 1; // 1
      month -= 1; // JS

      if (divider && (divider.includes('') || divider.includes(''))) {
        // 
        const [, endMonth, endDay] = datesForEvent.splice(i + 1, 1).flat();
        const endDayNormalized = endDay || 1;
        startAndEndDates.push(
          moment([year, month, day]).format('YYYYMMDD'),
          moment([year, endMonth - 1, endDayNormalized]).format('YYYYMMDD'),
        );
      } else if (divider && divider.includes('')) {
        startAndEndDates.push(
          moment([2019, 2, 1]).format('YYYYMMDD'),
          moment([year, month, day]).format('YYYYMMDD'),
        );
      } else {
        startAndEndDates.push(
          moment([year, month, day]).format('YYYYMMDD'),
          moment([year, month, day]).format('YYYYMMDD'),
        );
      }
      const dateSpan = moment.range(startAndEndDates);

      newEvent.title = item[''];
      newEvent.keywords = item[''];
      newEvent.text = item['/'];
      newEvent.excerpt = item['/'];
      newEvent.event_calendar_area = item[''];
      newEvent.tags = item[''];
      newEvent.event_calendar_spot = item[''];
      newEvent.event_calendar_image = item[''];
      newEvent.event_calendar_date = item[''];
      newEvent.event_calendar_address = item[''];
      newEvent.event_calendar_time = item[''];
      newEvent.event_calendar_parking = item[''];
      newEvent.event_calendar_contact = item[''];
      newEvent.event_calendar_start_day = moment(dateSpan.start).format('YYYYMMDD');
      newEvent.event_calendar_end_day = moment(dateSpan.end).format('YYYYMMDD');
      newEvent.event_calendar_info = item[''];
      newEvent.event_calendar_price = item[''];

      if (dateSpan.diff('days') === 1) {
        Object.keys(dateSpan).forEach((k) => {
          const subEvent = new EventContent(); // 
          subEvent.title = item[''];
          subEvent.keywords = item[''];
          subEvent.text = item['/'];
          subEvent.excerpt = item['/'];
          subEvent.event_calendar_area = item[''];
          subEvent.tags = item[''];
          subEvent.event_calendar_spot = item[''];
          subEvent.event_calendar_image = item[''];
          subEvent.event_calendar_date = item[''];
          subEvent.event_calendar_address = item[''];
          subEvent.event_calendar_time = item[''];
          subEvent.event_calendar_parking = item[''];
          subEvent.event_calendar_contact = item[''];
          subEvent.event_calendar_info = item[''];
          subEvent.event_calendar_price = item[''];
          subEvent.event_calendar_start_day = moment(dateSpan[k]).format('YYYYMMDD');
          subEvent.event_calendar_end_day = moment(dateSpan[k]).format('YYYYMMDD');
          eventCollection.push(subEvent);
        });
      } else {
        eventCollection.push(newEvent);
      }
    });
    return eventCollection.flat();
  });
}
